body{
    background-color: rgb(152, 101, 59);
    color: white;
}
h1,h3{
    text-align: center;
}
h1{
    font-size: 60px;
}
h3{
    color: rgb(40, 251, 163);
}
.game-row{
    text-align: center;
    height: 10px;
}
.cell, .alive, .born .dead{
    background-color: rgb(64, 61, 61);
    width: 10px;
    height: 10px;
    display: inline-block;
    border: 0.2px rgb(241, 241, 241) solid;
    border-radius: 10px;
}
.alive{
    background-color: rgb(0, 255, 48);
}
.born{
    background-color: rgb(207, 35, 195);
}

.button{
width: 150px;
margin-left: 30px;
margin-top: 10px;
}



